import type { LoaderArgs } from '@remix-run/cloudflare';
import { json, redirect } from '@remix-run/cloudflare';

import { RouteLayout } from '~/components/ui/RouteLayout';
import { getUserContextStorage } from '~/storages/userContext.server';

export const loader = async ({ context, request, params }: LoaderArgs) => {
  const userContextStorage = await getUserContextStorage(request);
  const userContext = userContextStorage.getUserContext();

  userContext.user = {
    ...userContext.user,
    pathFromLogin: request.url,
  };

  if (!context.userContext.user?.isAuthenticated) {
    return redirect('/?login=true', {
      status: 302,
    });
  }

  return json({});
};

export default function Index() {
  return <RouteLayout variant='user' />;
}
